import React from 'react';
import { Slider} from '@mui/material';


interface MiniteSliderPros{
    sliderOrientation?: "horizontal" | "vertical" | undefined;
}

const MiniteSlider: React.FC<MiniteSliderPros> = ({ sliderOrientation="vertical" }) => {
  return (
   <Slider orientation={sliderOrientation}/>
  );
};

export default MiniteSlider;